import React from 'react';
import { useState } from 'react';
import ChangeDoms from './component/ChangeDoms.js';
import RalewayWoff2 from './fonts/logotypejp_mp_b_1.1.ttf';
import CssBaseline from '@material-ui/core/CssBaseline';
import { ThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import InputBase from '@material-ui/core/InputBase';
import SearchIcon from '@material-ui/icons/Search';
const raleway = {
  fontFamily: 'Raleway',
  fontStyle: 'normal',
  fontDisplay: 'swap',
  fontWeight: 'normal',
  src: `
    url(${RalewayWoff2}) format('truetype')
  `,
};

const theme = createMuiTheme({
  typography: {
    fontFamily: 'Raleway',
    fontSize: 17,
  },
  overrides: {
    MuiCssBaseline: {
      '@global': {
        '@font-face': [raleway],
      },
    },
  },
});
const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    alignItems: 'center',
  },
  input: {
    marginLeft: theme.spacing(1),
    flex: 1,
    fontSize: 30,
    padding: '6px 6px',
    '@media (max-width:767px)': {
      fontSize: 22,
    },
  },
  search: {
    fontSize: 50,
    '@media (max-width:767px)': {
      fontSize: 35,
    },
  },
  resize: {
    fontSize: 10,
    padding: '6px 0 6px',
  },
}));
const App = (props) => {
  const [userInput, setUserInput] = useState('');
  const [flag, setFlag] = useState(false);
  const classes = useStyles();
  const handleChange = (event) => {
    if (flag != true && event.target.value == 'prof') {
      setFlag(true);
    }
    setUserInput(event.target.value);
  };

  return (
    <ThemeProvider theme={theme}>
      <div id="all">
        <CssBaseline />
        {/* <div className="title">About Me</div> */}
        <div className="aboutMe">
          <img className="myIconImage" src={props.myIcon} alt="myIcon" />
          <div className="name">HOTAKE</div>
        </div>
        <Paper className={'positionInput'}>
          <InputBase
            className={classes.input}
            placeholder="search for my prof..."
            inputProps={{
              classes: {
                input: classes.resize,
              },
              'aria-label': 'search for my prof...',
            }}
            value={userInput}
            onChange={handleChange}
          />
          <SearchIcon className={classes.search} />
        </Paper>
        <div className="allContainer">
          <ChangeDoms userInput={userInput} flag={flag} />
        </div>
      </div>
    </ThemeProvider>
  );
};

export default App;
