import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import myIcon from './image/my.jpg';
import * as serviceWorker from './serviceWorker';
import { library } from '@fortawesome/fontawesome-svg-core'; //fontawesomeのコアファイル
import { fab } from '@fortawesome/free-brands-svg-icons'; //fontawesomeのbrandアイコンのインポート
import { fas } from '@fortawesome/free-solid-svg-icons'; //fontawesomeのsolidアイコンのインポート
import { far } from '@fortawesome/free-regular-svg-icons'; //fontawesomeのregularアイコンのインポート

library.add(fab, fas, far); //他のコンポーネントから簡単に呼び出せるようにするための登録処理

(function (event) {
  // keydownイベントに、Ev_KeyDown関数を登録
  // helpイベントはreturn falseで無効にする
  window.onkeydown = Ev_KeyDown;
  window.onhelp = function () {
    return false;
  };

  /**
   * Ev_KeyDown
   * デフォルトのキーダウンイベントを無効化する
   * @param なし
   * @return {boolean} false
   */
  function Ev_KeyDown(event) {
    // F1-F12キーであれば、無効化する(F1キー：112,... F12キー:123)
    if (event.keyCode >= 112 && event.keyCode <= 123) {
      alert('sorry u cant see...');
      // event.keyCode = null;
      event.returnValue = false;
      //falseを返さないと動いてしまう
      return false;
    }
  }
  document.oncontextmenu = function () {
    return false;
  };
})();
ReactDOM.render(
  <React.StrictMode>
    <App myIcon={myIcon} />
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
